import { useState } from 'react';
import React, { useCallback } from 'react'


 

    export default function Controls() {

      const [status, setstatus] = useState(false)
      const [active, setActive] = useState('Active')
    
      const handleEnable = () => {
        setstatus(false)
        setActive('Active')
      }
    
      const handleDisable = () => {
        setstatus(true)
        setActive('Not Active')
      };
    
      return (
        <div className='controls'>
          <div id="confetti">
          <button disabled={status ? true : false} onClick={() => {alert("Button is Active")}}>{active}</button>
          </div>
          <div className='enable'><h1 onClick={handleEnable}>Enable Button</h1></div>
    
      <div className="disable"><h1 onClick={handleDisable}>Disable Button</h1></div>
      <hr/>
                 
    
        
        </div>
      );
    }
   
